.parent-drawflow {
	display: flex;
	overflow: hidden;
	touch-action: none;
	outline: none;
}

.drawflow {
	width: 100%;
	height: 100%;
	position: relative;
	user-select: none;
	perspective: 0;

	.parent-node {
		position: relative;
	}

	.drawflow-node {
		display: flex;
		align-items: center;
		position: absolute;
		background: #01bcd6;
		width: 160px;
		min-height: 40px;
		border-radius: 4px;
		border: 2px solid black;
		color: black;
		z-index: 2;
		padding: 15px;

		&.selected {
			background: red;
		}

		&:hover {
			cursor: move;
		}

		.inputs,
		.outputs {
			width: 0px;
		}

		.drawflow_content_node {
			width: 100%;
			display: block;
		}

		.input,
		.output {
			position: relative;
			width: 20px;
			height: 20px;
			background: white;
			border-radius: 50%;
			border: 2px solid black;
			cursor: crosshair;
			z-index: 1;
			margin-bottom: 5px;
		}

		.input {
			left: -27px;
			top: 2px;
			background: yellow;
		}

		.output {
			right: -3px;
			top: 2px;
		}
	}

	svg {
		z-index: 0;
		position: absolute;
		overflow: visible !important;
	}

	.connection {
		position: absolute;
		pointer-events: none;
		aspect-ratio: 1 / 1;

		.main-path {
			fill: none;
			stroke-width: 10px;
			stroke: steelblue;
			pointer-events: all;

			&:hover {
				stroke: #1266ab;
				cursor: pointer;
			}

			&.selected {
				stroke: #43b993;
			}
		}

		.point {
			cursor: move;
			stroke: black;
			stroke-width: 2;
			fill: white;
			pointer-events: all;

			&.selected,
			&:hover {
				fill: #1266ab;
			}
		}
	}

	.main-path {
		fill: none;
		stroke-width: 5px;
		stroke: steelblue;
	}
}

.drawflow-delete {
	position: absolute;
	display: block;
	width: 30px;
	height: 30px;
	background: black;
	color: white;
	z-index: 4;
	border: 2px solid white;
	line-height: 30px;
	font-weight: bold;
	text-align: center;
	border-radius: 50%;
	font-family: monospace;
	cursor: pointer;

	&.drawflow > & {
		margin-left: -15px;
		margin-top: 15px;
	}
}

.parent-node {
	.drawflow-delete {
		right: -15px;
		top: -15px;
	}
}
