@import '../scss_helper/variables';

 
a {
	outline: 0 !important;
	&:hover {
		outline: 0 !important;
	}
	&:active {
		outline: 0 !important;
	}
	&:focus {
		outline: 0 !important;
		outline: none !important;
		box-shadow: none !important;
	}
}
button {
	outline: 0 !important;
}
