.uiform-wrap {
	.space05 {
		height: 5px;
		border: none;
		display: block;
		padding: 0;
		clear: both;
	}
	.space10 {
		height: 10px;
		border: none;
		display: block;
		padding: 0;
		clear: both;
	}
	.space13 {
		height: 12px;
		border: none;
		display: block;
		padding: 0;
		clear: both;
	}
	.space20 {
		height: 20px;
		border: none;
		display: block;
		padding: 0;
		clear: both;
	}
	.widget-header {
		border-bottom: 1px solid #cdcdcd;
		box-shadow: 0 1px 0 #fff;
		position: relative;
		text-shadow: 0 1px #fff;
		background: -moz-linear-gradient(center top, #f8f8f8 0%, #e8e8e8 100%) repeat scroll 0 0 rgba(0, 0, 0, 0);
		color: $color_1;
		min-height: 51px;
		>i {
			font-size: 16px;
			padding: 15px 10px 15px 11px;
			text-align: center;
			width: 40px;
			color: $color_2;
			float: left;
			font-weight: 200;
			color: $color_2;
			float: left;
			font-weight: 200;
		}
		h5 {
			font-size: 16px;
			margin: 0;
			padding: 15px 0 0;
			color: $color_2;
			float: left;
			font-weight: 200;
		}
	}
	.widget-buttons {
		float: right;
		padding: 15px 15px 0 0;
	}
	.widget {
		background: none repeat scroll 0 0 #f7f7f7;
		border: 1px solid #cdcdcd;
		border-radius: 3px 3px 3px 3px;
		box-shadow: 0 2px 2px -2px #ccc;
		position: relative;
	}
	.widget-body {
		padding: 14px 16px;
	}
	.widget-footer {
		padding: 14px 16px;
	}
	.table-striped {
		tbody {
			>tr {
				&:nth-child(odd) {
					>td {
						background-color: $background-color_1;
					}
					>th {
						background-color: $background-color_1;
					}
				}
			}
		}
	}
}
.no-padding-left {
	padding-left: 0 !important;
}
.no-margin-left {
	margin-left: 0 !important;
}
.margin-top-10 {
	margin-top: 10px;
}
.widget-header {
	h5 {
		color: $color_2;
		float: left;
		font-weight: 200;
	}
}
.sfdc-btn-group {
