			margin-right: 10px;
		}
	}
}
.uiform-editing-main {
	width: 100%;
	min-width: 1200px;
	.uiform-builder-fields {
		background: #263147;
		float: left;
		height: 100%;
		padding: 8px;
		width: 100%;
	}
	.divider2 {
		span {
			background: #334059;
			border-radius: 100%;
			bottom: 100%;
			box-shadow: 0 2px 4px #999;
			height: 30px;
			left: 50%;
			margin-bottom: -15px;
			margin-left: -25px;
			min-width: 137px;
			position: absolute;
			i {
				position: absolute;
				top: -7px;
				bottom: 4px;
				left: 4px;
				right: 4px;
				border-radius: 100%;
				text-align: center;
				line-height: 40px;
				font-style: normal;
				color: $color_8;
			}
		}
	}
}
.panel-heading {
	.accordion-toggle {
		&::after {
			font-family: $font-family_1;
			content: '\e114';
			float: right;
			color: $color_3;
		}
	}
	.accordion-toggle.collapsed {
		&::after {
			content: '\e080';
		}
	}
}
.uiform-sidebar-fields {
	background: #263147;
	height: 100%;
