		}
	}
	fieldset {
		border: 1px solid #666b75 !important;
		margin: 5px 0 !important;
		padding: 3px 0 0 !important;
		position: relative;
		legend {
			background-color: $background-color_2;
			border: medium none;
			color: $color_6;
			font-size: 11px;
			left: 10px;
			padding: 6px 12px;
			position: absolute;
			text-shadow: 1px 1px 1px #333;
			top: -17px;
			width: auto !important;
		}
