#zgfm-multistep-page {
	:root {
		--border-color: #cacaca;
		--background-color: #ffffff;

		--background-box-title: #f7f7f7;
	}

	.container-fluid {
		padding-right: 0;
		padding-left: 0;
	}

	// bootstrap overrides
	.sfdc-nav li a:focus,
	.sfdc-nav li a:hover {
		background: transparent;
	}

	/*modal*/
	.sfdc-modal-full {
		width: 100%;
	}
	.sfdc-modal-full .sfdc-modal-content {
		height: 100%;
		border: 0;
	}
	.sfdc-modal-full .sfdc-modal-body {
		overflow-y: auto;
	}
}

/*tabs*/
$white: #ffffff;
$offwhite: #f5f5f5;
$lightBlue: #01bcd6;
$lightBlueTrans: rgba(1, 188, 214, 0.85);
$purple: #9d21b2;
$purpleTrans: rgba(157, 33, 178, 0.85);
$orange: #cc6600;
$orangeTrans: rgba(204, 102, 0, 0.85);
$yellow: #f39c12;
$yellowTrans: rgba(243, 156, 18, 0.85);
$black: #333333;
$blackTrans: rgba(51, 51, 51, 0.85);
$bluedark: #263147;
$transition: 0.2s ease all;
$yellow2: #fdfd96;
$yellowTrans2: #fafaaf;
$green: #165815;
$greenTrans: rgba(22, 88, 21, 0.85);
$red: #6f131c;
$redTrans: #922f39;
$red2: #ac4b55;
$redTrans2: #942e38;

$pink: #FD6E8A;
$pinkTrans:rgb(206, 91, 114);

$darkBlue: #00748A;
$darkBlueTrans: #00748A;
$tabWrapWidth: 100%;
// tab styles
#zgfm_tab_flat_style {
	width: $tabWrapWidth;
	background: $offwhite;

	.sfdc-tab-content {
		padding: 10px 5px;
		position: relative;
		background: $lightBlue;
		.sfdc-tab-pane {
			position: relative;
			bottom: 10px;
		}
	}

	.sfdc-nav-tabs li.sfdc-active a,
	.sfdc-nav-tabs li.sfdc-active a:focus,
	.sfdc-nav-tabs li.sfdc-active a:hover {
		border: none;
		color: $white;
		background: none;
	}

	.sfdc-nav-tabs li {
		width: 10%;
		a {
			border-radius: 0;
			color: $white;
			border: none;
			padding: 15px 0;
			margin-right: 0;
		}
	}

	.sfdc-nav-tabs .shifter {
		width: 4%;
		transition: $transition;
	}

	.sfdc-nav-tabs .shifterBack {
		background: $purple;
		border-right: 1px solid $offwhite;
		&:hover {
			background: $purpleTrans;
			a:hover {
				background: transparent;
			}
		}
	}

	.sfdc-nav-tabs .shifterForward {
		border-left: 1px solid $offwhite;
		background: $yellow;
		&:hover {
			background: $yellowTrans;
			a:hover {
				background: transparent;
			}
		}
	}

	.tabAnchor {
		transition: $transition;
	}

	.tabInner {
		padding: 10px;
		margin: 10px;

		background: $white;
		border-radius: 10px;
	}

	.tabToggle {
		transition: $transition;
	}

	.firstPanel,
	.sfdc-nav-tabs li a.firstPanel,
	.sfdc-nav-tabs li.sfdc-active a.firstPanel,
	.sfdc-nav-tabs li.sfdc-active a.firstPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.firstPanel:hover {
		background: $lightBlue;
		&:hover {
			background: $lightBlueTrans;
		}
	}

	.secondPanel,
	.sfdc-nav-tabs li a.secondPanel,
	.sfdc-nav-tabs li.sfdc-active a.secondPanel,
	.sfdc-nav-tabs li.sfdc-active a.secondPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.secondPanel:hover {
		background: $purple;
		&:hover {
			background: $purpleTrans;
		}
	}

	.thirdPanel,
	.sfdc-nav-tabs li a.thirdPanel,
	.sfdc-nav-tabs li.sfdc-active a.thirdPanel,
	.sfdc-nav-tabs li.sfdc-active a.thirdPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.thirdPanel:hover {
		background: $orange;
		&:hover {
			background: $orangeTrans;
		}
	}

	.fourthPanel,
	.sfdc-nav-tabs li a.fourthPanel,
	.sfdc-nav-tabs li.sfdc-active a.fourthPanel,
	.sfdc-nav-tabs li.sfdc-active a.fourthPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.fourthPanel:hover {
		background: $yellow;
		&:hover {
			background: $yellowTrans;
		}
	}

	.fifthPanel,
	.sfdc-nav-tabs li a.fifthPanel,
	.sfdc-nav-tabs li.sfdc-active a.fifthPanel,
	.sfdc-nav-tabs li.sfdc-active a.fifthPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.fifthPanel:hover {
		background: $darkBlue;
		&:hover {
			background: $darkBlueTrans;
		}
	}

	.zgfmyourselectedform,
	.sfdc-nav-tabs li a.zgfmyourselectedform,
	.sfdc-nav-tabs li.sfdc-active a.zgfmyourselectedform,
	.sfdc-nav-tabs li.sfdc-active a.zgfmyourselectedform:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfmyourselectedform:hover {
		background: $black;
		&:hover {
			background: $blackTrans;
		}
	}
}

.uiform-editing-dashboard {
	#uiformc-menu-secmm {
		.uiformc-tab-content-inner {
			padding: 0px;
		}
		position: relative;
		overflow: hidden;
		#uifm_mm_connection_infobox {
			position: absolute;
			top: 0;
			right: -600px;
			width: 600px;
			height: 100%;
			background-color: #f1f1f1;
			box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
			transition: right 0.3s ease;
			padding: 20px;
			box-sizing: border-box;
			overflow-y: auto;
			z-index: 99;
			background-color: #60031f;
			color: #fff;
			&.open {
				right: 0;
			}
			#uifm_content {
				.uifm_main_rules {
					margin-top: 20px;
					margin-bottom: 20px;
					padding: 10px;
					background-color: #881135;
					 
				}
				.uifm_main_rules2 {
					margin-bottom: 20px;
				}

				.uifm_show_graphic {
					.uifm_container {
						display: flex;
						align-items: center;
						.uifm_node {
							background-color: lightblue;
							border: 2px solid darkblue;
							border-radius: 50%;
							display: flex;
							justify-content: center;
							align-items: center;
							font-size: 18px;
							font-weight: bold;
							margin: 0 20px;
							padding: 20px;
							text-align: center;
							min-width: 100px;
							min-height: 100px;
							max-width: 200px;
							word-wrap: break-word;
							white-space: pre-wrap;
							color: #60031f;
						}

						.uifm_arrow {
							font-size: 50px;
							font-weight: bold;
						}
					}
				}
			}
			#uifm_mm_connection_infobox_btn_close {
				position: absolute;
				top: 10px;
				right: 10px;
				background: none;
				border: none;
				font-size: 24px;
				cursor: pointer;
			}
			.uifm_mm_conn_adopts {
				clear: both;
				background-color: #8b1035;
				padding: 10px;
				#uifm_mm_connection_infobox_btn_delete {
				}
			}
		}
	}

	/* flowchart*/
	#zgfm-flow-section-wrapper {
		border: 1px solid #ccc;

		#zgfmFlowSection {
			position: relative;
			width: 100%;
			height: 800px;
			$color: hsla(0, 0%, 2%, 0.3);
			$accent-color: rgb(71, 71, 71);
			$width: 10px;
			background-color: rgb(45, 49, 51);
			background-image: linear-gradient($color 1px, transparent 0), linear-gradient(90deg, $color 1px, transparent 0), linear-gradient($accent-color 2px, transparent 0), linear-gradient(90deg, $accent-color 2px, transparent 0);
			background-size: $width $width, $width $width, $width * 5 $width * 5, $width * 5 $width * 5;

			#zgfm-flow-options {
				float: left;
				position: absolute;
				top: 10px;
				left: 10px;
				color: white;
				font-weight: bold;
				padding-left: 10px;

				z-index: 5;
			}

			.btn-export {
				float: right;
				position: absolute;
				top: 10px;
				right: 10px;
				color: white;
				font-weight: bold;
				border: 1px solid #0e5ba3;
				background: #4ea9ff;
				padding: 5px 10px;
				border-radius: 4px;
				cursor: pointer;
				z-index: 5;
			}
			.bar-zoom {
				float: right;
				position: absolute;
				bottom: 10px;
				right: 10px;
				display: flex;
				font-size: 24px;
				color: white;
				padding: 5px 10px;
				background: #555555;
				border-radius: 4px;
				border-right: 1px solid var(--border-color);
				z-index: 5;
			}
			.bar-zoom svg {
				cursor: pointer;
				padding-left: 10px;
			}
			.bar-zoom svg:nth-child(1) {
				padding-left: 0px;
			}
		}
	}

	.sfdc-nav-tabs li.sfdc-active a,
	.sfdc-nav-tabs li.sfdc-active a:focus,
	.sfdc-nav-tabs li.sfdc-active a:hover {
		border: none;
		color: $white;
		background: none;
	}

	.sfdc-nav-tabs li {
		a {
			border-radius: 0;
			color: $white;
			border: none;
		}
	}
	.zgfm_firstPanel,
	.sfdc-nav-tabs li a.zgfm_firstPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_firstPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_firstPanel:focus,
