	.sfdc-nav-tabs li.sfdc-active a.zgfm_secondPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_secondPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_secondPanel:hover {
		background: $purple;
		&:hover {
			background: $purpleTrans;
		}
	}

	.zgfm_thirdPanel,
	.sfdc-nav-tabs li a.zgfm_thirdPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_thirdPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_thirdPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_thirdPanel:hover {
		background: $orange;
		&:hover {
			background: $orangeTrans;
		}
	}

	.zgfm_fourthPanel,
	.sfdc-nav-tabs li a.zgfm_fourthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_fourthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_fourthPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_fourthPanel:hover {
		background: $yellow;
		&:hover {
			background: $yellowTrans;
		}
	}

	.zgfm_fifthPanel,
	.sfdc-nav-tabs li a.zgfm_fifthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_fifthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_fifthPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_fifthPanel:hover {
		background: $darkBlue;
		&:hover {
			background: $darkBlueTrans;
		}
	}
	
	
	.zgfm_sixthPanel,
	.sfdc-nav-tabs li a.zgfm_sixthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_sixthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_sixthPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_sixthPanel:hover {
		background: $green;
		&:hover {
			background: $greenTrans;
		}
	}
	
	.zgfm_seventhPanel,
	.sfdc-nav-tabs li a.zgfm_seventhPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_seventhPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_seventhPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_seventhPanel:hover {
		background: $red;
		&:hover {
			background: $redTrans;
		}
	}
	
	
	.zgfm_eighthPanel,
	.sfdc-nav-tabs li a.zgfm_eighthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_eighthPanel,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_eighthPanel:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfm_eighthPanel:hover {
		background: $pink;
		&:hover {
			background: $pinkTrans;
		}
	}
	
	.zgfmyourselectedform,
	.sfdc-nav-tabs li a.zgfmyourselectedform,
	.sfdc-nav-tabs li.sfdc-active a.zgfmyourselectedform,
	.sfdc-nav-tabs li.sfdc-active a.zgfmyourselectedform:focus,
	.sfdc-nav-tabs li.sfdc-active a.zgfmyourselectedform:hover {
		background: $bluedark;
		&:hover {
			background: $bluedark;
		}
	}
}

#uifm_mm_form_not_selected {
	position: absolute;
	background: #263147;
	width: 100%;
	height: 100%;
	z-index: 99;

	.content {
		background-color: #fff;
		padding: 20px;
		margin: 20px;
	}
}

.zgfm_mulstistep_mode {
	.uiform-settings-wizard {
		display: none !important;
	}
}

.drawflow-node {
	&:hover {
	}
}

.drawflow_uifm_opt_edit {
	position: absolute;
	top: 51px;
	width: 30px;
	height: 30px;
	right: 33px;
	background: #fff;
	border-radius: 20px;
	display: none;
	border: 1px solid #000;
	cursor: pointer;
	.fa-pencil {
		font-size: 21px;
		position: absolute;
		top: 3px;
		right: 5px;
		&:hover {
			font-weight: bold;
		}
	}
}

.drawflow_uifm_opt_delete {
	position: absolute;
	top: 51px;
	width: 30px;
	height: 30px;
	right: 72px;
	background: #fff;
	border-radius: 20px;
	display: none;
	border: 1px solid #000;
	cursor: pointer;
	.fa-trash {
		font-size: 24px;
		position: absolute;
		top: 1px;
		right: 5px;
		&:hover {
			font-weight: bold;
		}
	}
}

.uifm_mm_variables_section {
	text-align: center;
	.uifm_formname {
	}
}

.overlay-path {
	stroke: yellow;
	stroke-width: 2;
	fill: none;
	stroke-dasharray: 5, 5;
	animation: dash 0.5s linear infinite;
}
@keyframes dash {
	to {
		stroke-dashoffset: -15;
	}
}