.uifm-dradiobtn-item {
	position: relative;
	display: inline-block;
	margin: 10px 20px 10px 0;
}
.uifm-dradiobtn-item:first-child {
	margin-left: 0;
}
.uifm-dradiobtn-item:last-child {
	margin-right: 0;
}
.uifm-dradiobtn-item .uifm-dcheckbox-item-viewport {
	display: block;
}

.uifm-dradiobtn-item:hover .uifm-dcheckbox-item-previmg,
.uifm-dradiobtn-item:hover .uifm-dcheckbox-item-nextimg {
	display: block;
}
.uifm-dcheckbox-item-chkst .fa-check-circle-o,
.uifm-dcheckbox-item-chkst .fa-circle-o {
	font-size: 23px!important;
}

/*new*/
.uifm-dfield-spinner {
	display: flex!important;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	max-width: 300px; /* adjust max-width as needed */
	margin: 0 auto;
	height: 100%;
  }
  
  .uifm-dfield-spinner .uifm-dfield-minus,
  .uifm-dfield-spinner .uifm-dfield-plus {
    height: 100%;
    color: #333!important;
    line-height: 10px;
  }
  
  .uifm-dfield-spinner .uifm-dfield-minus:focus,
  .uifm-dfield-spinner .uifm-dfield-plus:focus {
	outline: none!important;
  }
  
  .uifm-dfield-spinner .uifm-dfield-input {
	flex: 1 1 auto;
	padding: 1px 10px;
	font-size: 16px;
	text-align: center;
	border: none;
	background-color: #fff;
	 
	height: 100%;
	color:#000;
	border-bottom: 1px solid #ccc;
    border-top: 1px solid #ccc;
  }
  
  @media screen and (max-width: 600px) {
	.uifm-dfield-spinner {
	  flex-direction: column;
	}
  }
  
  @media screen and (max-width: 100px) {
	.uifm-dfield-spinner .uifm-dfield-input[contenteditable="true"] {
	  pointer-events: none;
	  user-select: none;
	}
  }

