//settings
#zgfm-set-progress-bar {
	.uifm-wz-opt-sort {
		display: none;
	}
}

//preview

.zgfm-progress-bar {


z-index: 1;
position: relative;

	/* wizard theme 1 */
	.uiform-wiztheme1 .zgfm-pbar-steps {
		display: table;
		list-style: outside none none;
		margin: 0;
		padding: 0;
		position: relative;
		width: 100%;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li a {
		box-shadow: none;
		text-decoration: none;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li {
		display: table-cell;
		text-align: center;
		width: 1%;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li .uifm-number {
		/*background-color: #fff;*/
		/*border: 5px solid #ced1d6;*/
		border-radius: 100%;
		/*color: #546474;*/
		display: inline-block;
		font-size: 15px;
		height: 40px;
		line-height: 30px;
		position: relative;
		text-align: center;
		width: 40px;
		z-index: 2;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li::before {
		border-top: 4px solid #ced1d6;
		content: '';
		display: block;
		font-size: 0;
		height: 1px;
		overflow: hidden;
		position: relative;
		top: 21px;
		width: 100%;
		z-index: 1;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li.last-child::before {
		max-width: 50%;
		width: 50%;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li:last-child::before {
		max-width: 50%;
		width: 50%;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li:first-child::before {
		left: 50%;
		max-width: 51%;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-current::before,
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-complete::before,
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-current .uifm-number,
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-complete .uifm-number {
		/*border-color: #5293c4;*/
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-complete .uifm-number {
		/*color: #fff;*/
		cursor: default;
		transition: transform 0.1s ease 0s;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-complete .uifm-number::before {
		/*background-color: #fff;*/
		border-radius: 100%;
		bottom: 0;
		/*color: #87ba21;*/
		content: '';
		display: block;
		font-family: FontAwesome;
		font-size: 17px;
		left: 0;
		line-height: 30px;
		position: absolute;
		right: 0;
		text-align: center;
		top: 0;
		z-index: 3;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-complete:hover .uifm-number {
		/*border-color: #80afd4;*/
		transform: scale(1.1);
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-complete:hover::before {
		/*border-color: #80afd4;*/
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li .uifm-title {
		/*color: #949ea7;*/
		display: block;
		font-size: 14px;
		margin-top: 4px;
		max-width: 100%;
		table-layout: fixed;
		text-align: center;
		word-wrap: break-word;
		z-index: 104;
	}
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-complete .uifm-title,
	.uiform-wiztheme1 .zgfm-pbar-steps li.uifm-current .uifm-title {
		/*color: #2b3d53;*/
	}

	/* wizard theme 0 */
	.uiform-wiztheme0 .zgfm-pbar-steps {
		border-radius: 3px;
		list-style: none outside none;
		margin: 0;
		overflow: hidden;
		padding: 0;
		position: relative;
	}
	.uiform-wiztheme0 .zgfm-pbar-steps li {
		display: inline-block;
		float: left;
		margin: 0;
		padding: 0;
		position: relative;
		outline: none;
	}

	.uiform-wiztheme0 .zgfm-pbar-steps li a {
		display: inline-block;
		font-size: 16px;
		height: 46px;
		line-height: 46px;
		margin: 0;
		padding: 0 20px 0 30px;
		text-decoration: none;
		width: auto;
	}

	/* just for backend */
	.uiform-wiztheme0 .zgfm-pbar-steps li a {
		cursor: pointer !important;
	}
	

	.uiform-wiztheme0 .zgfm-pbar-steps li a:focus {
		box-shadow: none !important;
	}
	.uiform-wiztheme0 .zgfm-pbar-steps li a,
	.uiform-wiztheme0 .zgfm-pbar-steps li a:hover,
	.uiform-wiztheme0 .zgfm-pbar-steps li a:active {
		/*background: #ecf0f1;
		color: #95a5a6;*/
		cursor: default;
		outline: none;
	}
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-number {
		/*background-color: #4798e7;
		color: #ecf0f1;*/
	}
	.uiform-wiztheme0 .zgfm-pbar-steps li.uifm-current a:focus {
		box-shadow: none !important;
	}
	.uiform-wiztheme0 .zgfm-pbar-steps li.uifm-current a,
	.uiform-wiztheme0 .zgfm-pbar-steps li.uifm-current a:hover,
	.uiform-wiztheme0 .zgfm-pbar-steps li.uifm-current a:active {
		/*background: #4798e7;
		color: #ecf0f1;*/
		cursor: default;
		outline: none;
	}
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-number {
		/*background-color: #ecf0f1;*/
		border-radius: 10px;
		/*color: #95a5a6;*/
		display: inline-block;
		float: left;
		font-size: 12px;
		font-weight: 700;
		line-height: 1;
		margin-right: 10px;
		margin-top: 15px;
		min-width: 10px;
		padding: 3px 6px;
		text-align: center;
		white-space: nowrap;
	}
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-number::before {
		position: absolute;
		top: -1px;
		right: -14px;
		display: block;
		z-index: 2;
		border: 24px solid transparent;
		border-right: 0;
		/*border-left: 14px solid #ecf0f1;*/
		content: ' ';
	}
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-number::after {
		position: absolute;
		top: -1px;
		right: -14px;
		display: block;
		z-index: 1;
		border: 24px solid transparent;
		border-right: 0;
		/*border-left: 14px solid #4798e7;*/
		content: ' ';
	}
	.uiform-wiztheme0 .zgfm-pbar-steps a:first-child {
		border-bottom-left-radius: 3px;
		border-top-left-radius: 3px;
	}
	
	
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-current a,
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-current a:hover,
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-current a:active {
		/*background: #ecf0f1;
		color: #ecf0f1;*/
		cursor: default;
	}
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-current .uifm-number::before {
		/*border-left-color: #4798e7;*/
	}
	.uiform-wiztheme0 .zgfm-pbar-steps .uifm-current .uifm-number::after {
		/*border-left-color: #4798e7;*/
	}
	
	//theme 3
	$primary-color: #63a2cb;
$secondary-color: #67d5bf;
	/*progressbar*/
.uiform-wiztheme3 .zgfm-pbar-steps {
	 
	display: flex; /* Use flexbox to handle equal width for each step */
    counter-reset: step;
    list-style-type: none;
    padding: 0;
    margin: 0;
	li {
		flex: 1; /* Each li will take equal width */
        /*color: white;*/
        text-transform: uppercase;
        font-size: 9px;
        position: relative;
        text-align: center; /* Center content horizontally */
	}
	li:before {
		content: counter(step);
		counter-increment: step;
		width: 20px;
		line-height: 20px;
		display: block;
		font-size: 10px;
		/*color: #333;*/
		/*background: white;*/
		border-radius: 3px;
		margin: 0 auto 5px auto;
		text-align: center;
	}
	li:after {
		content: '';
		width: 100%;
		height: 2px;
		/*background: white;*/
		position: absolute;
		left: -50%;
		top: 9px;
		z-index: -1; /*put it behind the numbers*/
	}
	
	li:first-child:after {
		/*connector not needed before the first step*/
		content: none; 
	}
	
	li.uifm-current,
	li.uifm-complete{
		&:before,
		&:after{
			/*background: $secondary-color;
			color: white;*/
		}
	}
	
	 
}
  
 
 
	
}
 