@import '../scss_helper/variables';

 
.uiform-wrap {

	@mixin navtabs {
		border-bottom: 1px solid #ddd;
			margin-top: 0;
			padding-left: 10px;
			padding-top: 5px;
			line-height: 1.43 !important;
			@content;
	}
	
	@mixin navtabsatag {
		background: linear-gradient(#fff, #ccc) repeat scroll 0 0 rgba(0, 0, 0, 0);
		border-radius: 5px 5px 0 0;
		margin-right: 7px;
		padding: 10px 10px;
		color: #838282;
			@content;
	}
	@mixin navtabsataghover {
		background: linear-gradient(#dcdcdc, #f9f9f9) repeat scroll 0 0 rgba(0, 0, 0, 0);
		border-top: 1px solid #f1f1f1;

		/* box-shadow: 1px 1px 5px #8c8c8c; */
		position: relative;
		z-index: 0;
		border-bottom: none;
		line-height: 1.41 !important;
		color:#000;
			@content;
	}
	
	#uiformc-menu-sec5{
		 
		.sfdc-nav-tabs{
			@include navtabs
			
			> li {
				
				&.sfdc-active{
					 > a{
					    @include navtabsataghover
					    &:hover, 
					    &:focus{
					        @include navtabsataghover
					    }
					 
					 }
				}
				
				> a {
					@include navtabsatag
					 
				}
			}
		}
	}
	
	#uiform-set-form-email{
		.sfdc-nav-tabs{
			@include navtabs
			> li {
				
				&.sfdc-active{
					 > a{
					    @include navtabsataghover
					    &:hover, 
					    &:focus{
					        @include navtabsataghover
					    }
					 
					 }
				}
				
				> a {
					@include navtabsatag
					/*background: #ccc;
					border: 1px solid #d3d3d3;
					border-bottom: none;*/
				}
			}
		}	
	}
	
	 

	
.uiform-editing-main {
	display: flex;
    height: 87vh; /* 100% of the viewport height */

	.sfdc-nav-tabs{
		padding-left: 10px;
	}
	.tab-content {
		position: relative;
		z-index: 1;
	}
	  .uiform-builder-preview {
		background: #263147;
		 
	
		height: 100%;
		padding: 8px 0;
		width: 100%;
	}
	.sfdc-nav-tabs > li.sfdc-active > a,
	.sfdc-nav-tabs > li.sfdc-active > a:hover,
	.sfdc-nav-tabs > li.sfdc-active > a:focus {
		background: linear-gradient(#565e66, #334059) repeat scroll 0 0 rgba(0, 0, 0, 0);
		border-top: 1px solid #565e66;
		border-color: #565e66;

		/* box-shadow: 1px 1px 5px #8c8c8c; */
		position: relative;
		z-index: 0;
		border-bottom: none;
		color: $color_5;
		line-height: 1.41 !important;
	}

	.sfdc-nav-tabs {
		border-bottom: 1px solid #334059;
		margin-top: 0;
		padding-left: 3px;
		padding-top: 5px;
		line-height: 1.42 !important;
	}

	.sfdc-nav-tabs > li > a {
		background: #eee;
		border: 1px solid #333b4a;
		border-bottom: none;
	}
	.sfdc-nav-tabs > li > a:hover {
		border: 1px solid #333b4a;
		border-bottom: none;
	}
	.sfdc-nav-tabs > li > a {
		background: linear-gradient(#334059, #565e66) repeat scroll 0 0 rgba(0, 0, 0, 0);
		border-radius: 5px 5px 0 0;
		margin-right: 7px;
		padding: 10px 10px;
		color: #ccc;

		/* text-shadow: 1px 1px #FFFFFF; */
	}
	
	
	
	/* panel builder */
.uifm-edit-panel {
	 
	
	/* min-height: 787px; */

	position: relative;
}
.uifm-edit-panel-left {
	//width: 33.3333%;
	max-width: 280px;
	flex: 1;
	.nav-tabs > li.active > a,
	.nav-tabs > li.active > a:hover,
	.nav-tabs > li.active > a:focus {
	   background-color: #f9f9f9;
   }
   background: #263147;
}
.uifm-edit-panel-center {
	//width: 33.3333%;
	flex: 1;
	overflow: hidden;
}
.uifm-edit-panel-right {
	flex: 1;
	background: #263147;
	//width: 33.3333%;
	padding: 8px;
	max-width: 730px;
}
	
	
	
	
}
	
	
 

	
	/* tab settings */
	.uiform-builder-data .nav-tabs > li.active > a,
	.uiform-builder-data .nav-tabs > li.active > a:hover,
	.uiform-builder-data .nav-tabs > li.active > a:focus {
		background-color: #f9f9f9;
	}
	.uiform-builder-data .uiform-tab-content {
		background-color: #334059;
	}
	

	#uiformc-menu-sec5 .divider2 span,
	#uiformc-menu-sec3 .divider2 span,
	#uiform-set-form-email .divider2 span {
		background: #f9f9f9;
		border-radius: 100%;
		bottom: 100%;
		box-shadow: 0 2px 4px #999;
		height: 30px;
		left: 50%;
		margin-bottom: -15px;
		margin-left: -25px;
		min-width: 137px;
		position: absolute;
	}
	#uiformc-menu-sec5 .divider2 span i,
	#uiformc-menu-sec3 .divider2 span i,
	#uiform-set-form-email .divider2 span i {
		position: absolute;
		top: -7px;
		bottom: 4px;
		left: 4px;
		right: 4px;
		border-radius: 100%;
		text-align: center;
		line-height: 40px;
		font-style: normal;
		color: #999;
	}
	
	#uiformc-menu-sec7 {
		.tabs-left{
			background-color: #FFFFFF;
			li{
				
				a{
					color:#000;
				}
			}
		}
	}
}

/* another tab style */
.uiformc-menu-wrap-2 .sfdc-nav-tabs > li > a {
	background: #2c4e94;
	background-image: linear-gradient(to bottom, #263147, #2c4e94) !important;
	border-radius: 5px 5px 0 0;
	margin-right: 7px;
	padding: 10px 10px;
	text-shadow: 1px 1px #fff;
	color: #ccc;
	text-shadow: 1px 1px #000;
}

.uiformc-menu-wrap-2 .sfdc-nav-tabs > li > a:hover,
.uiformc-menu-wrap-2 .sfdc-nav-tabs > li > a:focus {
	color: #fff !important;
}

.uiformc-menu-wrap-2 .sfdc-nav-tabs > li.sfdc-active > a,
.uiformc-menu-wrap-2 .sfdc-nav-tabs > li.sfdc-active > a:hover,
.uiformc-menu-wrap-2 .sfdc-nav-tabs > li.sfdc-active > a:focus {
	background: #263147;
	background-image: linear-gradient(to bottom, #2c4e94, #263147) !important;
	border-top: 1px solid #565e66;
	border-color: #565e66;

	/* box-shadow: 1px 1px 5px #8c8c8c; */
	position: relative;
	z-index: 0;
	border-bottom: none;
	color: #fff !important;
}

.uiform-sidebar-fields .tab-content {
	background: #f9f9f9;
}
