#zgfm-back-listform-maintable-container {
	clear: both;
}

.uiform-page_records {
	margin-bottom: 40px;

	.records-option-list {
		list-style-type: none; /* Remove default bullets */
		padding: 0 0 0 20px;
		margin: 10px 0;

		li {
			display: flex;
			align-items: center;
			font-size: 16px;
			color: #333; /* Text color */
			padding: 5px 0;
		}

		li i {
			color: #4caf50; /* Icon color */
			margin-right: 8px; /* Space between icon and text */
		}
	}
}
