.zgfm-license-val-form {
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
	font-size: 16px;
	max-width: 360px;
	margin: 40px auto 40px;
	background: #fff;
	padding: 40px;
	border-radius: 4px;

	.sfdc-btn.disabled, .sfdc-btn[disabled], fieldset[disabled] .sfdc-btn {
		cursor: not-allowed;
		filter: alpha(opacity=65);
		-webkit-box-shadow: none;
		box-shadow: none;
		opacity: .65;
	}

	.sfdc-btn {
		display: inline-block;
		padding: 6px 12px;
		margin-bottom: 0;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.42857143;
		text-align: center;
		white-space: nowrap;
		vertical-align: middle;
		-ms-touch-action: manipulation;
		touch-action: manipulation;
		cursor: pointer;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		background-image: none;
		border: 1px solid transparent;
		border-radius: 4px;
		color: #fff;
	}

	.sfdc-btn-block {
		display: block;
		width: 100%;
	}

	.sfdc-btn-primary {
		background-color: #8e44ad;
		border-color: #8e44ad;
	}

	.form-group {
		position: relative;
		padding-top: 16px;
		margin-bottom: 16px;

		.animated-label {
			position: absolute;
			top: 20px;
			left: 0;
			bottom: 0;
			z-index: 2;
			width: 100%;
			font-weight: 300;
			opacity: 0.5;
			cursor: text;
			transition: 0.2s ease all;
			margin: 0;
			-webkit-user-select: none;
			-moz-user-select: none;
			user-select: none;

			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 45%;
				height: 2px;
				width: 10px;
				visibility: hidden;
				background-color: #8e44ad;
				transition: 0.2s ease all;
			}
		}

		&.not-empty {
			.animated-label {
				top: 0;
				font-size: 12px;
			}
		}

		.form-control {
			position: relative;
			z-index: 1;
			border-radius: 0;
			border-width: 0 0 1px;
			border-bottom-color: rgba(0, 0, 0, 0.25);
			height: auto;
			padding: 3px 0 5px;

			&:focus {
				box-shadow: none;
				border-bottom-color: rgba(0, 0, 0, 0.12);

				~ .animated-label {
					top: 0;
					opacity: 1;
					color: #8e44ad;
					font-size: 12px;

					&:after {
						visibility: visible;
						width: 100%;
						left: 0;
					}
				}
			}
		}
	}
}

.error-message {
	color: #ff0000;
	font-size: 14px;
	margin-top: 10px;
	opacity: 0;
	transition: opacity 0.5s ease-in-out;

	&.show {
		opacity: 1;
	}
}
